.neon-crimson {
  color: var(--crimson);
  text-shadow: 0 0 3px var(--crimson-glow),
    0 0 8px var(--crimson-glow),
    0 0 16px var(--crimson-glow);
}
.card {
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0) 60%), var(--card);
  border: 1px solid rgba(225, 6, 60, 0.18);
  border-radius: 14px;
  box-shadow: 0 10px 25px rgba(0,0,0,0.35),
    inset 0 1px 0 rgba(255,255,255,0.03);
}
.btn-accent {
  background: var(--crimson);
  color: #0a0a0e;
  border-radius: 9999px;
  transition: box-shadow .2s ease, transform .05s ease;
}
.btn-accent:hover {
  box-shadow: 0 0 12px var(--crimson-glow), 0 0 28px var(--crimson-glow);
}
.btn-accent:active {
  transform: translateY(1px);
}
a {
  text-underline-offset: 3px;
}
a:hover {
  text-decoration: underline;
}
::selection {
  background: rgba(225, 6, 60, 0.35);
  color: #fff;
}
